@font-face {
    font-family: 'yintzuyuan_title_01-VariableVF';
    src: url('data:font/ttf;base64,') format('woff2');
}

.variable-weight-text {
    font-family: 'yintzuyuan_title_01-VariableVF';
    font-variation-settings: "AXS1" 50, "AXS2" 50;
    text-align: center;
    transition: font-variation-settings 0.3s ease;
}